/* GLOBAL */

:root {
    --background-color: #171717;
    --lines-color: rgba(255, 255, 255, 0.1);
    --hover-color: rgba(236, 236, 236, 0.781);
    --accent-color: rgba(196, 196, 196, 0.438);
    --frosted-glass-color: rgba(255, 255, 255, 0.01);
    --smoked-white: rgb(245, 245, 245);
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Montserrat';
    scroll-behavior: smooth;
}

html,
body {
    overflow-x: hidden;
}

body {
    position: relative;
    color: white;
    text-align: center;
    height: 100%;
    width: 100%;
    line-height: 1.6;
    overflow-x: hidden;
    cursor: default;
    
}

a {
    color: white;
    text-decoration: none;
}

/* REUSABLE */

.social_content_item {
    margin: 0;
    transition: 200ms;
}

.social_content_item:hover {
    color: var(--hover-color);
}

